.page-footer {
    background-color: @light;
    margin: 0;
    padding: 0;
    position: relative;
}
.page-footer__wrapper {
    padding: 0 20px;
    padding-top:20px;
    @media(min-width: @tablet) {
        width:728px;
        margin: 0 auto;
    }
    @media(min-width: @desktop) {
        width: 1210px;
        display: grid;
        grid-template-columns: 287px 739px;
        grid-template-rows: 1fr;
        column-gap: 170px;
        padding-top: 0;

    }
}

.page-footer span ,.contacts__time-item ,.contacts__adress-item ,.contacts__item {
    padding-bottom: 15px;
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 13px;
    color: @dark;
    text-align: center;
    @media(min-width: @desktop) {
        text-align: left;
    }
}

.page-footer__nav {
    display: none;
    @media(min-width: @tablet)  {
        display: block;
        margin: 0;
        background-color: @dark;
    }
}

.page-footer__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 auto;
    width: 728px;
    @media(min-width: @desktop) {
        width: 1200px;
    }
}
.page-footer__item a {
    display: block;
    padding: 12px 0;
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 12px;
    line-height: 12px;
    color: @white; 
    &:hover {
        color: @black;
    }
    &:active {
        color: @black;
        opacity: 50%;
    }
    @media(min-width: @desktop) {
        font-size: 14px;
        line-height: 13px;
    }
    
}
.page-footer__item {
    padding: 0;
   
    &--img {
        display: none;
    }
    @media(min-width: @desktop) {
    &--img {
        display: block;
        margin-left: 312px;
        margin-right: 207px;
    }
}
}
.page-footer__logo {
    display: none;
    @media(min-width: @desktop) {
        display: block;
        padding: 0;
    }
}
.page-footer__link-img {
    padding: 17px 0;
}