.slider__aside {
    display: none;
    @media(min-width: @desktop) {
        display: block;
        width: 380px;
        position: relative;
        @shift-aside: -293px;
        @shift-aside-bottom:400px;
        margin-top: @shift-aside;
        margin-bottom: @shift-aside-bottom;
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(15px);
        border-radius: 0px 40px 0px 0px;

    }
}

.slider__aside-title {
    margin: 0;
    padding: 30px 54px 24px 30px ;
    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    font-style: italic;
    font-weight: 600;
    font-size: 40px;
    line-height: 50px;
    color: @white;
    
}
.slider__aside-text {
    margin: 0;
    padding: 0 36px 0 30px;
    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    font-size: 13px;
    line-height: 20px;
    color: @white;
}

.slider__aside-scale {
    margin: 40px 0 61px 30px;
    width: 240px;
    height: 4px;
    background: rgba(255, 255, 255, 0.25);
}

.slider__aside-bar {
    width: 40px;
    height: 3px;
    background-color: @white;
}

.slider__aside-button {
   position: absolute;
   left: 360px;
   top: 100px;
   width: 54px;
   height: 54px;
   background-color: transparent;
   border-radius: 50%;
   border:none;
   background-image: url("../../img/button.svg");
}

.slider__aside-number {
    display: none;
    position: absolute;
    left: 310px;
    bottom: 40px;
    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    font-style: italic;
    font-weight:bold;
    font-size: 40px;
    line-height: 50px;
    color: rgba(255, 255, 255, 0.25);
    &--current {
        display: block;
    }
}

.slider {
    display: flex;
    flex-direction: column;
}
.slider__buttons {
    width: 140px;
    display: flex;
    justify-content: space-between;
    align-self: center;
    justify-self: center;
}
.slider__button {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: @dark;
    opacity: 35%;
    padding: 0;
    border:0;
    &--current {
        opacity: 100%;
    }
    @media(min-width: @desktop) {
        display: none;
    }
}

