.contacts {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 25px;
    position: relative;
    
    @media(min-width: @desktop) {
        margin: 0;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 350px;
            height: 350px;
            background-image: url("../img/Vector.svg");
            right: -1100px;
            top: -150px;
        }
    }
}

.contacts__list ,.contacts__adress-list ,.conacts__time-list {
    margin: 0;
    padding: 0;
    @media(min-width: @desktop) {
        padding-left: 20px;
        list-style: disc;
        text-align: left;
    }
}

.contacts__adress-item {
        &:last-child {
            display: none;
        }
        @media(min-width: @desktop) {
            &:last-child {
                display: block;
            }
        }
}

.contacts__phone {
    padding-bottom: 20px;
    color: @dark;
}

.contacts__title {
    margin-bottom: 30px;
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 20px;
    line-height: 27px;
    color: @dark;
    text-align: center;
    position: relative;
    display: inline-block;
    @media(min-width: @tablet) {
    &::after {
        content: "";
        position: absolute;
        width: 26px;
        height: 2px;
        background-color: @dark;
        bottom: -10px;
        right: 350px;
        @media(min-width: @desktop) {
            display: none;
        }
    }
}
       
    @media(min-width: @desktop) {
        margin-bottom: 20px;
        margin-top: 25px;
        font-size: 32px;
        line-height: 43px;
        text-align: left;
        &--contact{
            margin-top: 40px;
        }
    }
}

.contacts__adress {
 display: flex;   
 flex-direction: column;
 margin-bottom: 25px;
 @media(min-width: @desktop) {
    margin: 0;
}
}

.contacts__time {
    display: flex;   
    flex-direction: column;
    margin-bottom: 45px;
    @media(min-width: @desktop) {
        margin: 0;
    }
}

.social{
    display: flex;   
    flex-direction: column;
    text-align: center;
}


.copyright {
    &--extra {
        display: none;
    }
    @media(min-width: @desktop) {
        display: none;
        margin: 0;
        &--extra {
            display: block;
            margin-top: 33px;
        }
    }
}

.copyright__wrapper {
    display: flex;   
    flex-direction: column;
    flex-wrap: wrap;
}