.social__wrapper {
    display: none;
    @media(min-width: @desktop) {
        display: block;
    }
}

.social__title {
    @media(min-width: @desktop) {
    font-size: 32px;
    line-height: 43px;
    margin: 40px 0 30px 0;
}
}
.social__list {
    display: grid;
    grid-template-columns: 170px 170px 170px 170px;
    column-gap: 20px;
    grid-template-rows: 115px 115px 115px 115px;
    row-gap: 10px;
}

.social__item-3 {
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: 2;
    grid-column-start: 3;
}

.social__item-1{
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: -1;
    grid-column-start: 1;
}

.social__item-5{
    grid-row-start: 3;
    grid-row-end: 5;
    grid-column-start: -1;
    grid-column-start: 1;
} 

.social__item-6 {
    grid-row-start: 2;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-start: 2;
}

.social__item-7 {
    grid-row-start: 2;
    grid-row-end: 4;
    grid-column-start: 3;
    grid-column-start: 4;
}

.social__item-10 {
    grid-column-start: 3;
    grid-column-start: 4;
}