.toggle-main {
    position: relative;
  display: block;
  width: 24px;
  height: 20px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  @media(min-width: @desktop) {
    width: 40px;
}
}

.toggle-main--closed::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 0px;
    width: 24px;
    height: 2px;
    background-color: @black;
    box-shadow: 0 8px 0 0 @black, 0 16px 0 0 @black;
    @media(min-width: @desktop) {
        width: 38px;
        box-shadow: 0 9px 0 0 @black, 0 18px 0 0 @black;
    }
  }