.info {
    padding: 0 20px;
    margin: 0 auto;
    @media(min-width: @tablet) {
        display: flex;
        width:728px;
        margin: 0 auto;
        padding: 0 20px;
        border-top: 1px solid @black;
    }
    @media(min-width: @desktop) {
        width: 1200px;
        padding: 0;
        border: none;
        
    }
}

.info__slider {
    border-top: 1px solid @black;
    
    @media(min-width: @tablet) {
        order: 10;
        padding-left: 40px;
        margin-bottom: 50px;
        border: none;
    }

    @media(min-width: @desktop) {
        position: absolute;
        right: 0;
        top: 0;
      }
}
.info__wrapper {
    @media(min-width: @tablet) {
        padding: 50px 0 40px 0;
        max-width: 278px;
    }
    @media(min-width: @desktop) {
        min-width: 550px;
        padding-top: 100px;
    }
}

.info__slogan {
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 30px;
    line-height: 40px;
    text-align: right;
    @media(min-width: @tablet) {
        text-align: right;
        margin: 0;
    }
    @media(min-width: @desktop) {
        font-size: 56px;
        line-height: 70px;
    }
}

.info__text {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    line-height: 18px;
    text-align: right;
    @media(min-width: @tablet) {
        font-size: 14px;
        margin: 40px 0;
        margin-right: 0;
    }
    @media(min-width: @desktop) {
        display: inline-block;
        margin: 60px 0;
        position: relative;
        font-size: 22px;
        line-height: 150%;
        margin-left: 0;
        padding-left: 200px;
        &::before {
            content: "";
            position: absolute;
            width: 180px;
            height: 180px;
            background-image: url("../img/music.svg");
            background-repeat: no-repeat;
            left: 0;
            top: -40px;
            animation-name: rotate, move;
            animation-duration: 3s, 6s;   
        }
    }
}

@keyframes rotate {
    50% {
      transform: rotate(360deg);
    }
  }
  @keyframes move {
    0% {
      top: -100px;
    }
     50% {
      top:100px;
    }
    100% {
        top:-40px;
      }
  }

.info__img {
    width: 100%;
    padding: 20px 0;
    @media(min-width: @tablet) {
        padding-top: 30px;
        width: 370px;
        min-height: 320px;
        align-self: center;        
    }
    @media(min-width: @desktop) {
      width: 900px;
      height: 760px;
      padding-top: 0;
    }
}

.info__link {
    display: block;
    padding: 35px 0 20px;
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 28px;
    line-height: 38px;
    font-weight: bold;
    color: @dark;
    &:hover {
        color:rgba(74, 74, 74, 0.7);
    }
    &:active {
        color:rgba(74, 74, 74, 0.2);
    }
    
    &--design{ 
        padding-right: 6px;
        transform: rotate(-20deg);
    }
    &--shop{
        color: @white;
        &:hover {
            color: @white;
            opacity: 70%;
        }
        &:active {
            color: @white;
            opacity: 30%;
        }
    }
    @media(min-width: @tablet) {
        padding: 26px 5px 0 6px;
        font-size: 18px;
        line-height: 24px;
        &--design{ 
            transform: none;
        }
    }
    @media(min-width: @desktop) {
        font-size: 26px;
        line-height: 35px;
        padding: 20px 15px;
        transform: none;
        &--design{ 
            padding: 20px 5px 20px 5px;
        }     
    }
}
.info__link-wrapper {
    display: flex;
    justify-content: space-between;
    @media(min-width: @tablet) {
        width: 190px;
        margin-left: 100px;
    }
    @media(min-width: @desktop) {
        width: 280px;
        margin: 0;
        margin-left: 290px;
    }

}
.info__circle {
  border-radius: 100%;
  font-size: 15px;
  text-align: center;
  height: 120px;
  width: 120px;
  color: #fff;
  border: 2px solid @dark;
  &:hover {
    border: 2px solid rgba(74, 74, 74, 0.5);
}
&:active {
    border: 2px solid rgba(74, 74, 74, 0.2);
}

  &--shop{
      background-color: @dark;
      color: @white;
      &:hover {
          border: 2px solid transparent;
          background-color: rgba(74, 74, 74, 0.5);
    }
    &:active {
        border: 2px solid rgba(74, 74, 74, 0.2);
    }
  }
  @media(min-width: @tablet) {
    padding: 0px;
    width: 80px;
    height: 80px;
    &--shop {
        width: 80px;
        height: 80px;
    }
  }  
  @media(min-width: @desktop) {
    height: 80px;
    width: 80px;
    padding: 20px;
    text-align: center;
  }
}