.page-header__container {
    @media(min-width: @tablet) {
        margin: 0 auto;
        width:728px;
    }
    @media(min-width: @desktop) {
        width: 1210px;
    }
}

.page-header__logo-link {
    display: block;
}
.page-header__search {
    fill: none;
    
    @media(min-width: @desktop) {
        width: 40px;
        height: 40px;
        stroke-width: 1;
    }
}

.page-header__logo {
    @media(min-width: @desktop) {
        width: 100%;
        height: auto;
    }
}

.page-header__wrapper {
    padding: 24px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media(min-width: @desktop) {
        width: 550px;

    }
}