.design {
 padding: 0 20px;
 margin-top: 120px;
 @media(min-width: @tablet) {
     margin: 0;
 }
 @media(min-width: @desktop) {
    margin-top: 170px;
}
}

.design__main-wrapper {
    @media(min-width: @tablet) {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width:728px;
        margin: 0 auto;
        
    }
    @media(min-width: @desktop) {
        width: 1210px;
        padding: 0;
    }
    
}

.design__wrapper {
    display: flex;
    flex-direction: column;
    margin-top: 131px;
    text-align: left;
    &:first-child {
        margin-top: 120px;
    }
    &:last-child{
        margin-bottom: 71px;
    }
    &:nth-child(2n) {
        text-align: right;
    }
   
    @media(min-width: @tablet) {
        flex-direction: row;
        justify-content: space-between;
        margin-top: 80px;
        &:first-child {
            margin-top: 0px;
        }
        &:last-child{
            margin-bottom: 80px;
        }
        &:nth-child(2n) {
            flex-direction: row-reverse;
        }
    }

    @media(min-width: @desktop) {
        margin-top: 120px;
        &:last-child{
            margin-bottom: 120px;
        }
    }
    
}

.design__wrapper-inside {
    @media(min-width: @tablet) {
        width: 330px;
        
    }
    @media(min-width: @desktop) {
        width: auto;
        padding: 0;
    }
}
.design__img {
    width: 100%;
    height:auto;
}

.design__pic-wrapper {
    @media(min-width: @desktop) {
        width: 690px;
       min-height:  460px;
    }
}

.design__wrapper h2 {
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 30px;
    line-height: 40px;
    font-weight: normal;
    color: @dark;

    @media(min-width: @tablet) {
        margin:0;
    }
    @media(min-width: @desktop) {
        font-size: 56px;
        line-height: 125%;
    }
}

.design__wrapper p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    line-height: 18px;
    text-align: left;
    
    @media(min-width: @tablet) {
        font-size: 14px;
        text-align: revert;
    }
    @media(min-width: @desktop) {
        display: inline-block;
        font-size: 22px;
        line-height: 150%;
        width: 450px;
        margin-left: 0;
    }
}


.design__link {
    display: block;
    padding-top: 31px;
    padding-left: 120px;
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 22px;
    line-height: 29px;
    text-align: left;
    color: @dark;
    position: relative;
    &:hover {
        color: rgba(74, 74, 74, 0.5);
    }
    &:active{
        color:  rgba(74, 74, 74, 0.3);
    }
    &::before{
        content: "";
        position: absolute;
        width: 80px;
        height: 80px;
        background-color: @dark;
        border-radius: 50%;
        left: 10px;
        bottom: -15px;
        
        &:active{
            background-color:  rgba(74, 74, 74, 0.3);
        }
        @media(min-width: @tablet) {
            width: 60px;
            height: 60px;
            bottom: -5px;
        }
        @media(min-width: @desktop) {
            width: 120px;
            height: 120px;
            left: 350px;
            bottom: -20px;
        }
        
    }
    &::after {
        content: "";
        position: absolute;
        width: 50px;
        height: 37px;
        left: 23px;
        bottom: 10px;
        background-image: url("../img/eye.svg");
        background-repeat: no-repeat;
        @media(min-width: @tablet) {
            width: 38px;
            height: 28px;
            left: 23px;
            background-image: url("../img/eyet.svg");
            bottom: 10px;
            left: 21px;
        }
        @media(min-width: @desktop) {
            background-image: url("../img/eyed.svg");
            width: 73px;
            height: 55px;
            left: 372px;
            bottom: 17px;
        }
    }
    @media(min-width: @tablet) {
        font-size: 18px;
        line-height: 24px;
    }
    @media(min-width: @desktop) {
        font-size: 32px;
        line-height: 43px;
        width: 203px;
    }
    &:hover {
        color: rgba(74, 74, 74, 0.5);
    }
    &:active{
        color:  rgba(74, 74, 74, 0.3);
    }
}

.design__circle-wrapper {
    display: flex;
    flex-direction: column;
    @media(min-width: @tablet) {
        width: 360px;
        align-self: center;
        flex-direction: row-reverse;
        justify-content: space-between;
        margin: 40px;
        margin-bottom: 0;
    }
    @media(min-width: @desktop) {
        position: relative;
        margin-top: 40px;
        &::after {
            position: absolute;
            content: "";
            width: 116px;
            height: 116px;
            background-image: url("../img/mio__inside.png");
            left: -492px;
            bottom: 167px;
        }
        &::before {
            position: absolute;
            content: "";
            width: 250px;
            height: 250px;
            background-image: url("../img/mio__desk.svg");
            left: -560px;
            bottom: 100px;
        }
    }
}

.design__circle {
    margin-bottom: 70px;
    align-items: center;
    border-radius: 100%;
    font-size: 15px;
    padding: 20px;
    text-align: center;
    display: flex;
    height: 100px;
    width: 100px;
    background-color: @dark;
    align-self: flex-end;
    &:hover {
        background-color: rgba(74, 74, 74, 0.5);
    }
    &:active {
        background-color: 1px solid rgba(74, 74, 74, 0.2);
    }
    
    &--works {
      align-self: flex-start;
      width: 120px;
      height: 120px;
      border: 1px solid @dark;
      background-color: @white;
      &:hover {
        background-color: @white;
        border: 1px solid rgba(74, 74, 74, 0.5);
    }
    &:active {
        background-color: @white;
        border: 1px solid rgba(74, 74, 74, 0.2);
    }
      @media(min-width: @tablet) {
          width: 100px;
          height: 100px;
      }
    }
}
.design-link {
    font-family: "Playfair Display", Arial, Helvetica, sans-serif;
    font-size: 18px;
    line-height: 24px;
    color: @white;
    &--works  {
        color:@dark;
    }
   
}